import {WinJSDec, fnError} from './APP/config';

var name = 'CUSTOMER_EVENT_UPLOAD';
var CUSTOMER_EVENT_UPLOAD = (fn = _ => {
}) => {
  if (typeof fn === 'function') {
    WinJSDec(_ => {
      document.addEventListener('CHARTBAR_EVENT_UPLOAD', fn);
    })
  } else {
    throw new TypeError(`[${name}] ${fnError}`);
  }
};
export default CUSTOMER_EVENT_UPLOAD;
